
.breadcrumbs {
    padding: 30px 0;
}

.breadcrumbs-list {
    display: flex;
    align-items: center;
}

.breadcrumbs__item {
    padding-right: 16px;
    a {
        color: #8A8A8A;
        font-size: 14px;
        line-height: 14px;
        font-weight: 350;
        &:hover {
            text-decoration: underline;
        }
    }
    &:last-of-type {
        padding-right: 0;
        pointer-events: none;
        a {
            color: #141414
        }
    }
}
