.product-head {
    padding: 10px 0 30px;
    .container {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__action {
        margin-left: 50px;
        display: flex;
        align-items: center;
        gap: 15px;
        button {
            background-color: #fff;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            border: 1px solid rgba(#1C1C1C, .2);
            outline: none;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            svg {
                width: 18px;
                height: 18px;
                display: block;
            }
            &:hover {
                background-color: #1C1C1C;
                svg {
                    path {
                        fill: #fff;
                    }
                }
            }
        }
    }
}

.product__articul {
    margin: 0 0 0 auto;
    font-size: 14px;
    line-height: 14px;
}

.product-main__item {
    background: url('../images/item-1.png') center center / cover no-repeat;
    min-height: 720px;
    padding: 40px 0;
    position: relative;
}

.product-main__3d {
    width: 55px;
    height: 55px;
    border-radius: 50%;
    background-color: #03A543;
    color: #fff;
    padding: 18px 14px;
    text-align: center;
    font-size: 20px;
    line-height: 20px;
    position: absolute;
    left: 40px;
    top: 40px;
}

.product-main__title {
    font-size: 52px;
    line-height: 52px;
    font-weight: 500;
    margin-bottom: 20px;
}

.product-main__info {
    position: absolute;
    right: 40px;
    bottom: 170px;
    @media(max-width: 1600px) {
        bottom: 80px;
    }
}

.product-main__subtitle {
    font-size: 20px;
    line-height: 20px;
}

.product-about {
    padding: 200px 0;
}

.product-about__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 40px;
}

.product-about__left {
    padding: 70px 60px;
    background: #F9F9F9;
    border-radius: 16px;
}

.product-about__title {
    font-size: 48px;
    line-height: 60px;
    margin-bottom: 50px;
    font-weight: 500;
}

.product-about__desc {
    margin-bottom: 30px;
}

.product-about__action {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 40px;
}


.product-options {
    margin-bottom: 40px;
}

.product-options__title {
    font-weight: 500;
    margin-bottom: 15px;
}


.product-tech {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-bottom: 60px;
}

.product-parameters {
    &__title {
        font-size: 28px;
        line-height: 28px;
        margin-bottom: 60px;
        font-weight: 500;
    }
    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-gap: 60px;
    }
    &__item {
        color: #8A8A8A;
        font-size: 20px;
        line-height: 22px;
        span {
            display: block;
            margin-bottom: 18px;
            font-weight: 500;
            font-size: 22px;
            line-height: 1;
            color: #000;
        }

    }
}


.product-video {
    background: url('../images/video-item.png') center center / cover no-repeat;
    padding: 80px 0;
    .container {
        position: relative;
        min-height: 740px;
    }
}

.product-video__title {
    position: absolute;
    top: 0;
    left: 40px;
    color: #fff;
    font-size: 48px;
    line-height: 1;
    font-weight: 500;
}

.product-video__btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    span {
        display: block;
        color: #fff;
        font-size: 24px;
        line-height: 1;
        margin-top: 30px;
    }
}

.product-video__info {
    position: absolute;
    right: 40px;
    bottom: 0;
    max-width: 169px;
    text-align: right;
    color: #fff;
    opacity: 1;
    font-weight: 500;
    span {
        display: block;
        margin-bottom: 20px;
        color: #FFFFFF;
        opacity: .4;
    }
}

.product-bracing {
    padding: 180px 0 90px;
}

.product-bracing__content {
    display: flex;
    align-items: center;
    gap: 106px;
    @media(max-width: 1600px) {
        gap: 60px;
    }
}

.product-bracing__img {
    max-width: 1027px;
    width: 100%;
    overflow: hidden;
    border-radius: 15px;
    img {
        width: 100%;
        display: block;
        object-fit: cover;
    }
}

// .product-bracing__info {
//     padding-left: 106px;
// }

.product-bracing__title {
    max-width: 400px;
    font-weight: 500;
    font-size: 48px;
    line-height: 57px;
    margin-bottom: 50px;
}

.product-bracing__type {
    margin-bottom: 25px;
    font-size: 24px;
    line-height: 24px;
    padding-left: 40px;
    cursor: pointer;
    &:hover {
        opacity: .7;
    }
    &.active {
        font-weight: 500;
        background: url('../images/arrow-tab.svg') left center / 25px no-repeat;
    }
    &:last-of-type {
        margin-bottom: 0;
    }
}

.product-colors {
    padding: 90px 0 180px;
}

.product-colors__content {
    display: flex; 
    align-items: center;
    gap: 106px;
    @media(max-width: 1600px) {
        gap: 60px;
    }
}

.product-colors__img {
    max-width: 1027px;
    width: 100%;
  
    img {
        width: 100%;
        object-fit: cover;
        display: block;
    }
}

.product-colors__title {
    font-size: 48px;
    line-height: 48px;
    margin-bottom: 70px;
    font-weight: 500;
}

.product-colors__info {
    max-width: 587px;
    width: 100%;
}

.product-colors__panel {
    padding: 40px 50px;
    background: #F5F5F5;
    border-radius: 20px;
}

.product-colors__subtitle {
    margin-bottom: 50px;
}