*,body {
    box-sizing: border-box;
    font-family: 'Stem', sans-serif;
    margin: 0;
    padding: 0;
    font-size: 18px;
    line-height: 26px;
    font-weight: 350;
    height: auto;

}

body {
    &.no_scroll {
        height: 100vh;
        overflow: hidden;
    }
}
ul {
    padding-left: 0;
}
li {
    list-style: none;
}
a {
    text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    padding: 0;
}


.container {
    max-width: 1800px;
    padding: 0 40px;
    margin: 0 auto;
    @include sm-block {
        padding: 0 20px;
    }
}

.btn {
    display: inline-block;
    border: none;
    outline: none;
    border-radius: 8px;
    padding: 0 20px;
    height: 80px;
    background: #03A543;
    color: #fff;
    font-size: 20px;
    line-height: 80px;
    text-align: center;
    cursor: pointer;
    &--border {
        border: 1px solid #1C1C1C;
        color: #1C1C1C;
        background-color: transparent;
    }
    &:hover {
        opacity: .7;
    }
}